
import { useState, useEffect, useRef } from 'react';
import Button from 'react-bootstrap/Button';
import './nft_view.css';
import Form from 'react-bootstrap/Form';

function Nft_view(props) {

    const [nft_image, set_nft_image] = useState("https://cdn.discordapp.com/attachments/704350607234039938/1104112376208425000/image.png");
    const [nft_name, set_nft_name] = useState("NFTNFT");
    const [nft_description, set_nft_description] = useState("5/11NFT");

    const mint_nft = async () => {
        try{

            props.setWait_show(true);
            props.cont.nft_transfer();
            props.cont.nft_mint(nft_name, nft_description,nft_image);
        }catch (error){
            console.log(error);
            props.setWait_show(false);
        }
       
    }

        
    return (
        <div className='row'>
            <div className='col-2' />
            <div className='nft_container col-8' >
                <Form>
                    <Form.Group >

                        <Form.Control type="url" placeholder="url" value={nft_image} onChange={(event) => set_nft_image(event.target.value)} />

                        <div class="nft-details__top-section">
                            <div class="box nft-details__card" >
                                <img class="nft-details__image" src={nft_image} alt="nft_image" />
                            </div>
                            <div class="box nft-details__info box--flex-direction-column box--justify-content-space-between box--display-flex">

                                <div>
                                    <h6 class="box box--margin-top-1 box--margin-bottom-2 box--flex-direction-row typography nft-details__description typography--h6 typography--weight-bold typography--style-normal typography--color-text-default">
                                        
                                    </h6>
                                    <h4 class="box box--margin-top-1 box--margin-bottom-2 box--flex-direction-row typography typography--h4 typography--weight-bold typography--style-normal typography--color-text-default">

                                        <Form.Control type="text" placeholder="NFT" value={nft_name} onChange={(event) => set_nft_name(event.target.value)} />
                                    </h4>
                                </div>
                                <div>
                                    <h6 class="box box--margin-top-1 box--margin-bottom-2 box--flex-direction-row typography nft-details__description typography--h6 typography--weight-bold typography--style-normal typography--color-text-default">
                                        
                                    </h6>
                                    <h6 class="box box--margin-top-1 box--margin-bottom-4 box--flex-direction-row typography typography--h6 typography--weight-normal typography--style-normal typography--color-text-alternative typography--overflowwrap-break-word">
                                        <Form.Control
                                            as="textarea"
                                            rows={nft_description.split("\n").length + 3}
                                            value={nft_description}
                                            onChange={(e) => set_nft_description(e.target.value)}
                                        />
                                    </h6>
                                </div>

                            </div>
                        </div>
                    </Form.Group>
                    <Button variant="primary" onClick={() => mint_nft()} style={{ marginTop: '20px' }}>
                        NFT
                    </Button>
                </Form>
            </div>
            <div className='col-2' />
        </div>
    )
}

export default Nft_view;


