import { React, useEffect ,useState} from 'react';
import { Navbar, Nav, Container } from 'react-bootstrap';


function NavigationBar(props) {

    const [my_address, set_my_address] = useState(null);
    
    const get_variable = async () => {//
        set_my_address(await props.cont.get_account());

        
    }

    useEffect(() => {
        get_variable();
    }, []);
    
    return (
        <Navbar bg="light" expand="lg">
            <Container>
                <Navbar.Brand href="#">NFT</Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="me-auto">
                        <Nav.Link href={process.env.PUBLIC_URL + "/oiginal_nft_mint"}>NFT</Nav.Link>
                        <Nav.Link href={process.env.PUBLIC_URL + "/mint"}>MINT</Nav.Link>
                        <Nav.Link href={process.env.PUBLIC_URL + "/owned_token/"+my_address}></Nav.Link>
                    </Nav>
                </Navbar.Collapse>
            </Container>
        </Navbar>
    );
}

export default NavigationBar;
