
import { useState, useEffect, useRef } from 'react';
import Button from 'react-bootstrap/Button';
import Wait_Modal from './components/wait_Modal';
function Mint_page(props) {
    const [wait_show, setWait_show] = useState(null);
    const mint_nft = async () => {
        try{
            await props.cont.mint_nft();
            setWait_show(true);
            props.cont.siawase_mint_event();
        }catch (error){
            console.log(error);
            props.setWait_show(false);
        }
       
    }
    return (
        <div>
            <h1></h1>
            <Button variant="primary" onClick={() => mint_nft()} style={{ marginTop: '20px' }}>
                NFT
            </Button>
            <Wait_Modal showFlag={wait_show}  />
        </div>
    )
}

export default Mint_page;