import token_contract from "./token_abi.json";
import nft_contract  from "./nft_abi.json";
import { token_address ,nft_address} from "./config"
import { BigNumber, ethers } from "ethers";
import { parseUnits, shallowCopy } from 'ethers/lib/utils';
import { useEffect } from 'react';
const { ethereum } = window;



const token_abi = token_contract.abi;
const nft_abi=nft_contract.abi;

// function Contracts_MetaMask(){
const provider = new ethers.providers.Web3Provider(window.ethereum);
const signer = provider.getSigner();
const Token_Contract = new ethers.Contract(token_address, token_abi, signer);
const NFT_Contract = new ethers.Contract(nft_address, nft_abi, signer);
const listener =null;
const transfer_filters = Token_Contract.filters["Transfer"];
const nft_transfer_filters = NFT_Contract.filters["Transfer"];
class Contracts {
    
    async get_chain_id() {
        const chainId = await provider.getNetwork();
        return chainId["chainId"];
    }
    async get_account() {
        const accounts = await ethereum.request({ method: 'eth_requestAccounts' });
        return accounts[0];
    }
    async change_network() {
        const networkParam = {
            chainId: '0x13881',
            chainName: 'polygon mumbai',
            nativeCurrency: { name: 'MATIC', symbol: 'MATIC', decimals: 18 },
            rpcUrls: ['https://rpc-mumbai.maticvigil.com/'],
            blockExplorerUrls: ['https://mumbai.polygonscan.com/'],
        };
        await window.ethereum.request({
            method: 'wallet_addEthereumChain',
            params: [networkParam],
        });
        window.location.reload();
    }

    async isMetaMaskConnected() {
        const {ethereum} = window;
        const accounts = await ethereum.request({method: 'eth_accounts'});
        return accounts && accounts.length > 0;
    }

    async connectWallet () {
		await window.ethereum.request({ method: "eth_requestAccounts" });
        window.location.reload();
	}

    


    async mint_nft() {
        const account = this.get_account();
        await Token_Contract.mint(account);
    }

    async siawase_mint_event() {
        const account = await this.get_account();
        
        
        // 
        provider.once(transfer_filters(null,account,null), (from, to, tokenId, event) => {
          console.log("hit")
          console.log(account, from, to, tokenId, event);
          window.location.href = process.env.PUBLIC_URL+"/owned_token/"+account;
        });
        
       
    }




    async get_owned_tokens(address) {
        return Token_Contract.getOwnedTokens(address);
    }
    async get_tokenURI(tokenId) {
        console.log(tokenId)
        return Token_Contract.tokenURI(tokenId);
    }
    async transfer(to, tokenId) {
        const account = await this.get_account();
        await Token_Contract.transferFrom(account, to, tokenId);
    }

    async event_transfer() {
        const account = await this.get_account();
        
        
        // 
        provider.once(transfer_filters(account), (from, to, tokenId, event) => {
          console.log("hit")
          console.log(account, from, to, tokenId, event);
          window.location.reload();
        });
        
       
    }

    async stop_event_transfer(){
        console.log(listener);
        const account = await this.get_account();
        console.log(provider.off(transfer_filters(account)));
        console.log(provider.listeners(transfer_filters(account)));

    }

    async nft_mint(name,description,image){
        NFT_Contract.mint(name,description,image)
    }

    async nft_transfer() {
        const account = await this.get_account();
        console.log("searching");
        console.log(nft_transfer_filters());
        // 
        provider.on(nft_transfer_filters(null,account,null), (event) => {
          console.log("hit")
          console.log(nft_address);
          console.log(parseInt(event.topics[3],16));
          //
          window.location.href = process.env.PUBLIC_URL+"/add_nft/"+nft_address+"/"+parseInt(event.topics[3],16);
          
        });
    }

}


export default Contracts;