import { useEffect, useState } from "react";

const Modal_change_network = (props) => {

  const [chain_id, setChain_id] = useState(null);
  const [is_connect, setIs_connect] = useState(false);
  const get_variable = async () => {//
    setChain_id(await props.cont.get_chain_id());
    //setIs_connect(await props.cont.isMetaMaskConnected());
  }

  useEffect(() => {
    get_variable();
  }, []);

  if (chain_id) {
    console.log(chain_id);
    console.log(is_connect)
    return (
      <>
        {(chain_id != 80001) ? ( // showFlagtrueModal
          <div id="overlay" class="stars" style={{}}>
            <div id="modalContent" style={{
              "color": "white", "position": "fixed",
              "top": "50%", /*  */
              "left": "50%",
              "transform": "translate(-50%, -50%)"
            }}>
              {/*  */}
              <h2 >Polygon mumbai</h2>
              <br />
              <button type="button" class="btn btn-dark" onClick={() => { props.cont.change_network() }} >Change Network</button>
            </div>
          </div>
        ) : (
          <>
          </>// showFlagfalseModal
        )}
      </>
    );
  }
  else {
    return (
      <>
      </>
    )
  }

};

export default Modal_change_network;
