import { useEffect, useState, useRef } from "react";
import logo from './logo.svg';
import './App.css';
import NavigationBar from "./pages/navbar/navbar";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import Contracts, { Contracts_MetaMask } from "./contract/contracts";
import 'bootstrap/dist/css/bootstrap.min.css';

//import
import Origina_NFT_page from "./pages/original_nft/main";
import Mint_page from "./pages/mint/main";
import Owned_tokens_page from "./pages/owned_tokens/main";
import Add_nft_page from "./pages/add_nft_guide/main";
import Modal_change_network from "./contract/Modal_change_network";
import Modal_login from "./contract/Modal_login";


function App() {
  const cont = new Contracts();

  const [chain_id, setChain_id] = useState(null);
  const [is_connect, setIs_connect] = useState(false);
  const get_variable = async () => {//
    setChain_id(await cont.get_chain_id());
    setIs_connect(await cont.isMetaMaskConnected());
  }

  useEffect(() => {
    get_variable();
    if (window.ethereum) {
      window.ethereum.on("chainChanged", () => {
        window.location.reload();
      });
      window.ethereum.on("accountsChanged", () => {
        window.location.reload();
      });
    }
  }, []);

  if (is_connect == true && chain_id == "0x13881") {
    return (

      <div className="App">


        <body>

          <div >

          <NavigationBar cont={cont} />
            <BrowserRouter basename={process.env.PUBLIC_URL} >
              <Routes  >
                <Route path={'/'} element={<Origina_NFT_page url={'origina_NFT'} cont={cont} />} />
              </Routes>
              <Routes  >
                <Route path={'/oiginal_nft_mint'} element={<Origina_NFT_page url={'origina_NFT'} cont={cont} />} />
              </Routes>
              <Routes >
                <Route path={'/mint'} element={<Mint_page url={'mint'} cont={cont} />} />
              </Routes>
              <Routes>
                <Route path={'/owned_token/:address'} element={<Owned_tokens_page url={'owned_token'} cont={cont} />} />
              </Routes>
              <Routes>
                <Route path={'/add_nft/:address/:tokenId'} element={<Add_nft_page url={'add_nft'} cont={cont} />} />
              </Routes>



            </BrowserRouter>
          </div>

        </body>


      </div>

    );
  } else if(chain_id != "0x13881") {
    return (
      <div className="App">

        <Modal_change_network cont={cont} />
      </div>
    );
  }
  else{
    return (
      <div className="App">

        <Modal_login cont={cont} />
      </div>
    );
  }

}

export default App;
